body{font-family:Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,SimSun,sans-serif !important;font-size:15px
}
.panel-block{border:1px solid #ebebeb;border-radius:3px;transition:.2s;padding:24px
}
.space-20{height:20px
}
.wrapper{background-color:#F8FAFF
}
.wrapper .page-header{position:relative !important;height:80px
}
.wrapper .page-header.collapse{background-color:#fff;box-shadow:0 10px 60px 0 rgba(29,29,31,0.07);opacity:0.98
}
.wrapper .nav-container{margin:0 auto;width:90%;color:#1D1D1F
}
.wrapper .nav-container .nav-left{float:left
}
.wrapper .nav-container .nav-left .logo{position:relative;height:80px;line-height:80px
}
.wrapper .nav-container .nav-left .nav-icon{display:none;position:absolute;top:50%;right:0;margin-top:-10px;font-size:20px
}
.wrapper .nav-container .nav-right{float:right;height:80px;display:-ms-flexbox;display:flex
}
.wrapper .nav-container .nav-right .navbar{display:inline-block;margin:0;padding:0;height:80px;font-size:14px;line-height:80px;list-style-type:none
}
.wrapper .nav-container .nav-right .navbar li{position:relative;display:inline-block;margin:0 24px;cursor:pointer;transition:color .3s
}
.wrapper .nav-container .nav-right .navbar a{display:inline-block;color:#1D1D1F
}
.wrapper .nav-container .nav-right .btn-logout{display:inline-block;margin:0 24px;padding:2px 12px;border-radius:2px;cursor:pointer;transition:color .3s, border .3s;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center
}
.wrapper .at-container{position:relative;overflow:auto;margin:2% auto;width:94%;background-color:#fff;box-shadow:0 4px 30px 0 rgba(223,225,230,0.5)
}
.el-table thead,.el-table td{color:#000 !important
}



